onKeyUpRoom(option, event) {
  let found = false;

  for (var i = 0; i < this.roomList.length; i++) {
    if (this.roomList[i].RoomType == option.key && event) {
      this.roomList[i].RoomCount = event;
      found = true;
    }
  }

  if(found) return;

  this.roomList.push({
    RoomType: option.key,
    RoomCount: event
  });
}
