Map() {
  this.source = new VectorSource({ wrapX: false });

  this.layer = new TileLayer({
    source: new OSM()
  });

  this.startMarker = new Feature({
    geometry: new Point(fromLonLat([6.661594, 50.433237]))
  });

  this.startMarker.setStyle(
    new Style({
      image: new Icon(
        /** @type {module:ol/style/Icon~Options} */ ({
          crossOrigin: "anonymous",
          anchor: [0.5, 0.96],
          src:
            "https://cdn3.iconfinder.com/data/icons/map-markers-1/512/residence-512.png",
          scale: 0.09
        })
      )
    })
  );

  this.mapView = new OlView({
    center: fromLonLat([69.2401, 41.2995]),
    zoom: 12
  });
  this.vectorLayer = new VectorLayer({
    source: this.source
  });

  this.map = new OlMap({
    target: "map",
    layers: [this.layer, this.vectorLayer],
    view: this.mapView
  });
  let self = this;
  this.map.on("moveend", function(e) {
    var map = e.map;

    var extent = map.getView().calculateExtent(map.getSize());

    // this.view.fit(this.vectorLayer.getSource().getExtent(), {duration: 1000})

    var bottomLeft = toLonLat(getBottomLeft(extent));

    var topRight = toLonLat(getTopRight(extent));
  });

  this.map.on(
    "click",
    function(e) {
      self.map.removeLayer(this.vectorLayer)
     
      let point=ol.proj.toLonLat(e.coordinate);

this.longitude=point[0].toString().replace('.',',');
  

this.latitude=point[1].toString().replace('.',',');

      this.startMarker = new Feature({
        geometry: new Point(e.coordinate)
      });

      this.startMarker.setStyle(
        new Style({
          image: new Icon(
            /** @type {module:ol/style/Icon~Options} */ ({
              crossOrigin: "anonymous",
              anchor: [0.5, 0.96],
              src:
                "https://cdn3.iconfinder.com/data/icons/map-markers-1/512/residence-512.png",
              scale: 0.09
            })
          )
        })
      );

      this.vectorLayer = new VectorLayer({
        source: new VectorSource({
          features: [this.startMarker]
        })
      });

      this.map.addLayer(this.vectorLayer);
    }.bind(this)
  );

  let draw = new Draw({
    source: this.source,
    type: "Polygon",
    freehand: true
  });

  // draw.on(
  //   "drawend",
  //   function(e) {
  //     var currentFeature = e.feature; //this is the feature fired the event
  //     var restOfFeats = this.source.getFeatures(); //rest of feats
  //     var allFeats = restOfFeats.concat(currentFeature); //concatenate the event feat to the array of source feats
  //     console.log(allFeats.length);
  //   }.bind(this)
  // );
  draw.on(
    "drawend",
    function() {
      this.map.addInteraction(draw);
    }.bind(this)
  );

}
